body {
  margin: 0;
}

img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  display: block;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

@font-face {
  font-family: "PT_Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "PT_Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2");
  font-display: swap;
}
.description {
  margin: 0;
  padding: 0;
}

.link {
  text-decoration: none;
  color: inherit;
}

.list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.title {
  margin: 0;
  padding-left: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  padding: 0 18px;
}

.page {
  height: 100%;
}

.page__body {
  height: 100%;
  font-family: "PT_Sans", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
  margin: 0;
  min-height: 100%;
  background-color: #EEEEEE;
}

.page__container {
  min-width: 320px;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  background-color: #FFFFFF;
}

.main-nav {
  min-height: 56px;
  font-family: "PT_Sans", "Tahoma", sans-serif;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
  position: relative;
  z-index: 2;
}
@media (min-width: 768px) {
  .main-nav {
    font-size: 14px;
    line-height: 21px;
  }
}

.main-nav__wrapper {
  width: clamp(320px, 100%, 1280px);
  margin: 0 auto;
  position: relative;
}

.main-nav__logo {
  width: fit-content;
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  display: none;
}
.main-nav__logo--closed-menu {
  display: block;
}

.main-nav__button {
  display: block;
  right: 0;
  margin: 0;
  margin-left: auto;
  padding: 0;
  border: none;
  width: 22px;
  height: 22px;
  background-color: #ffffff;
  position: absolute;
  top: 17px;
  right: 20px;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
}
.main-nav__button--cross {
  z-index: 1;
}
.main-nav__button--noscript {
  display: none;
}
@media (min-width: 768px) {
  .main-nav__button {
    display: none;
  }
}

.main-nav__list {
  position: absolute;
  width: inherit;
  text-align: center;
  background-color: #81B3D2;
  color: #ffffff;
}
.main-nav__list--close {
  display: none;
}
.main-nav__list--noscript {
  display: block;
}
@media (min-width: 768px) {
  .main-nav__list {
    position: static;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    background-color: inherit;
    color: #000000;
  }
}

@media (max-width: 767px) {
  .main-nav__item:hover {
    background-color: #669EC0;
  }
  .main-nav__item:active {
    background-color: #5496BD;
  }
}
.main-nav__item:not(:last-child) {
  box-shadow: 0px 1px 0px 0px rgba(1, 1, 1, 0.2), 0px 1px 0px 0px rgba(255, 255, 255, 0.2) inset;
}
@media (min-width: 768px) {
  .main-nav__item:not(:last-child) {
    box-shadow: none;
  }
}
.main-nav__item:last-child {
  position: relative;
}
.main-nav__item:last-child:after {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 7px;
  background-image: linear-gradient(180deg, transparent 0, rgba(0, 0, 0, 0.2) 100%);
}
@media (min-width: 768px) {
  .main-nav__item:last-child:after {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-nav__item {
    flex-basis: min(19.9vw, 240px);
  }
  .main-nav__item:nth-child(2), .main-nav__item:nth-child(6) {
    margin-right: min(19.9vw, 240px);
  }
}
@media (min-width: 1280px) {
  .main-nav__item {
    flex-basis: 240px;
  }
  .main-nav__item:nth-child(2), .main-nav__item:nth-child(6) {
    margin-right: 240px;
  }
}

.main-nav__link {
  display: block;
  padding: 17px 12px 12px 12px;
}
@media (min-width: 768px) {
  .main-nav__link {
    padding: 17px 18px 18px 18px;
  }
  .main-nav__link:hover {
    opacity: 0.6;
  }
  .main-nav__link:active {
    opacity: 0.3;
  }
}
@media (min-width: 768px) {
  .main-nav__link--active {
    color: #766357;
  }
  .main-nav__link--active:hover {
    opacity: 1;
  }
  .main-nav__link--active:active {
    opacity: 1;
  }
}

.main-logo__image {
  margin: 0 auto;
}

.button {
  margin: 0;
  padding: 0;
  border: none;
  background-color: #81B3D2;
  cursor: pointer;
}

.main-container {
  flex-grow: 1;
}

.hero {
  background-image: url("../img/back_desktop.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-color: #766357;
  min-height: 389px;
  position: relative;
  z-index: 0;
}
@media (min-resolution: 2dppx) {
  .hero {
    background-image: url("../img/back_desktop@2x.jpg");
  }
}
.hero::after {
  content: "";
  display: block;
  background-image: url("../img/icons/hero_botton_triangle.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  position: absolute;
  bottom: 0;
  width: 320px;
  height: 57px;
  z-index: 1;
}

.hero__title {
  margin: 90px auto 0 auto;
}